import uniPopup from '@/components/uni-popup/uni-popup.vue'
import uniIcon from '../../components/uni-icon/uni-icon.vue'
export default {
	components: {
		uniPopup,
		uniIcon
	},
	data() {
		return {
			usermessage: {},
			isCard: true,
			type: '',
			money:'',
			submit_readonly:true
		};
	},
	methods: {
		onLoad(options) {
			if (options.type == 0) {
				this.isCard = true;
				this.usermessage = {
					title: '银行卡提现',
					account: '工商银行'
				};
			} else {
				this.isCard = false;
				this.usermessage = {
					title: '支付宝提现',
					account: '186***7889'
				};
			}
		},
		charge(){
				this.type="payment"
		},
		selectCard() {},
		select_payment(payment) {
			this.type = payment;
		},
		radioChange(evt){
			console.log(evt.target.value);
		}
	},
	watch:{
		money(value){
			if(value!=''){
				this.submit_readonly=false
			}
		}
	}
};
